<template>
  <div class="active-dynamic-details-container">
    <!-- <div class="background-box"></div> -->
    <div class="container">
      <div class="active_detail_box">
        <div class="title_top">
          <div style="display: flex; align-items: center">
            <h3>{{ eventDetails.title }}</h3>
            <span
              @click="$router.push('/activityDetail?id=' + id)"
              v-if="isAdmin"
              style="
                margin-left: 10px;
                text-decoration: underline;
                color: #1989ff;
                font-size: 12px;
                flex-shrink: 0;
              "
              >编辑</span
            >
          </div>
          <p>
            主讲人:{{
              eventDetails.instructor_info
                ? eventDetails.instructor_info.name
                : ""
            }}
          </p>
        </div>
        <div class="flex active-section first">
          <span class="t-char">活动标签</span>
          <div>
            <div
              v-for="item in eventDetails.tag_info"
              :key="item.id"
              class="tag"
            >
              {{ item.name }}
            </div>
          </div>
        </div>
        <div class="flex active-section">
          <span class="t-char">举办单位</span>
          <div class="v-char">
            {{
              eventDetails && eventDetails.host_info
                ? eventDetails.host_info.name
                : ""
            }}
          </div>
        </div>
        <div class="flex active-section">
          <span class="t-char">活动时间</span>
          <div class="v-char">{{ yearsMonthsCmp }}</div>
        </div>
        <div class="flex active-section">
          <span class="t-char">活动时长</span>
          <div class="v-char">{{ activityTimeCmp }}小时</div>
        </div>
        <div class="flex active-section">
          <span class="t-char">活动地点</span>
          <div class="v-char">{{ areaCmp }}</div>
        </div>
        <div class="flex active-section">
          <span class="t-char">活动类型</span>
          <div class="v-char">{{ eventDetails.type }}</div>
        </div>
        <div class="flex active-section">
          <span class="t-char">形式标签</span>
          <div class="v-char">
            {{
              eventDetails.tagform_info ? eventDetails.tagform_info.name : ""
            }}
          </div>
        </div>
        <div class="flex active-section">
          <span class="t-char">对象标签</span>
          <div class="v-char">
            {{
              eventDetails.tagrole_info ? eventDetails.tagrole_info.name : ""
            }}
          </div>
        </div>
        <div class="flex active-section">
          <span class="t-char">上报数据单位</span>
          <div class="v-char">
            {{
              eventDetails.dataunit_info && eventDetails.dataunit_info.length
                ? eventDetails.dataunit_info[0].name
                : ""
            }}
          </div>
        </div>
        <div class="flex active-section">
          <span class="t-char">参与人数</span>
          <div class="v-char">{{ eventDetails.visitor }}</div>
        </div>
      </div>
      <!-- 现场照片 -->
      <div class="card-box">
        <p class="card-title-style">现场照片</p>
        <Card class="card-style">
          <ul class="flex flex-wrap">
            <li
              v-for="(item, index) of record2List"
              :key="index"
              class="card-photo-style"
            >
              <Avatar
                v-show="record2List.length > 0"
                :imgUrl="handleImgEmptyUrl(item)"
                :width="200"
                :height="200"
              />
            </li>
          </ul>

          <NoRecord v-show="!record2List || !record2List.length" />
        </Card>
      </div>
      <!-- 举办单位反馈表&主讲人承诺书 -->
      <div class="card-box">
        <p class="card-title-style">活动补充材料</p>
        <div class="flex">
          <Card class="card-style card-style-average flex-center">
            <Avatar
              v-show="eventDetails.file1"
              class="card-photo-style"
              :imgUrl="handleImgEmptyUrl(eventDetails.file1)"
              :width="240"
              :height="340"
            />
            <NoRecord v-show="!eventDetails.file1" />
          </Card>
          <Card class="card-style card-style-average flex-center">
            <Avatar
              v-show="eventDetails.file2"
              class="card-photo-style"
              :imgUrl="handleImgEmptyUrl(eventDetails.file2)"
              :width="240"
              :height="340"
            />
            <NoRecord v-show="!eventDetails.file2" />
          </Card>
        </div>
      </div>

      <!-- 活动海报 -->
      <div v-if="eventDetails.poster_img" class="card-box">
        <p class="card-title-style">活动海报</p>
        <Card class="card-style flex-center">
          <Avatar
            v-show="eventDetails.poster_img"
            :imgUrl="handleImgEmptyUrl(eventDetails.poster_img)"
            :width="240"
            :height="340"
          />
          <NoRecord v-show="!eventDetails.poster_img" />
        </Card>
      </div>

      <!-- 听众评价 -->
      <div class="card-box">
        <p class="card-title-style">听众评价</p>
        <Card class="card-style">
          <!-- <ul v-show="show" class="comments-list">
            <li
              v-for="(item, index) in 6"
              :key="index"
              class="comments-item flex-start align-start"
            >
              <Logo
                :imgUrl="handleImgEmptyUrl('@/assets/mock/avatar_test.png')"
                :size="65"
                round
              />
              <div class="comments-info-box">
                <p class="comments-username-style">李思怡</p>
                <p class="comments-content-style">
                  这场讲座，让我了解双减之后家长应该做什么，对我帮助很大。
                </p>
              </div>
            </li>
          </ul> -->
          <NoRecord />
        </Card>
      </div>
    </div>
    <div class="bottom-wrap-box flex">
      <div class="btn-white" @click="reqLectureVerifyAPI(-1)">拒绝备案</div>
      <div class="btn-blue" @click="reqLectureVerifyAPI(1)">已备案</div>
    </div>
  </div>
</template>

<script>
import { timeConversion } from "@/utils/common";

export default {
  components: {
    Card: () => import("@/components/Card"),
    Avatar: () => import("@/components/Avatar"),
    Logo: () => import("@/components/Logo"),
    NoRecord: () => import("@/components/NoRecord"),
  },
  computed: {
    isAdmin() {
      return this.$store.state.user.accountIdInfo.is_admin;
    },
    // 区域
    areaCmp() {
      const modelType = this.$store.getters.associationInfo.model_type;
      switch (modelType) {
        case "district":
          return `${this.eventDetails.district}${this.eventDetails.street}`;
          break;

        default:
          return `${this.eventDetails.city}${this.eventDetails.district}${this.eventDetails.street}`;
      }
    },
    // 日期
    yearsMonthsCmp() {
      return this.$dateFormat(
        this.eventDetails.start_date,
        "UTC:yyyy年mm月dd日 HH:MM"
      );
    },
    // 星期
    weekCmp() {
      return this.$dateFormat(this.eventDetails.start_date, "UTC:dddd");
    },
    // 日
    dayCmp() {
      return this.$dateFormat(this.eventDetails.start_date, "UTC:dd");
    },
    // 时-分
    timeCmp() {
      return this.$dateFormat(this.eventDetails.start_date, "UTC:HH:MM");
    },
    // 活动时长
    activityTimeCmp() {
      return Number(
        timeConversion(this.eventDetails.duration, "s", "h")
          .toString()
          .match(/^\d+(?:\.\d{0,2})?/)
      );
    },
  },
  data() {
    return {
      show: false,
      // 活动ID
      id: null,
      // 活动详情对象
      eventDetails: {
        instructor_info: {},
      },
      // 讲座模块-权限对象
      lecturePermissions: {},
      // 现场照片列表
      record2List: [],
    };
  },
  created() {
    this.id = this.$route.query.id;
    this.eventDetailsAPI(this.id);
    this.lecturePermissionsAPI(this.id);
    this.lectureRecord2ListAPI(this.id);
  },
  methods: {
    // async handleThemeTagView() {
    //   const themeTagList = this.$store.getters.themeTagList
    // },

    /**
     * 处理图片数据为空时的图片地址
     */
    handleImgEmptyUrl(imgUrl) {
      return imgUrl ? imgUrl : "";
    },
    async reqLectureVerifyAPI(status) {
      const data = {
        aid: this.$store.getters.associationInfo.id,
        id: this.id,
        status,
      };
      await this.$request("lecture.reqLectureVerify", data);
      this.$toast("操作成功");
    },
    /**
     * 讲座模块-获取信息(活动详情)API
     */
    async eventDetailsAPI(id) {
      const data = {
        id,
      };
      const res = await this.$request("lecture.eventDetails", data);
      console.log("讲座模块-获取信息(活动详情)API_res :>> ", res);
      this.eventDetails = res;
    },
    /**
     * 讲座模块-权限API
     * @param {String} lid 讲座ID
     */
    async lecturePermissionsAPI(lid) {
      const data = {
        lid,
      };
      const res = await this.$request("lecture.lecturePermissions", data);
      console.log("讲座模块-权限API_res :>> ", res);
      this.lecturePermissions = res;
    },
    /**
     * 记录2列表API
     */
    async lectureRecord2ListAPI(lid) {
      const data = {
        lid,
      };
      const res = await this.$request("lecture.lectureRecord2List", data);
      this.record2List = res[lid].filter((x) => {
        return x.indexOf("http") == 0;
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.active-dynamic-details-container {
  position: relative;
  .background-box {
    height: px(340);
    background: linear-gradient(180deg, #0077ff 0%, #f7f7f7 100%);
    opacity: 0.6;
  }
  .active_detail_box {
    // width: 702px;
    // height: 1047px;
    background: #ffffff;
    border-radius: px(16);
    opacity: 1;
    padding: px(32) px(18) px(32) px(24);
    .title_top {
      border-bottom: 1px solid #eeeeee;
      padding-bottom: px(24);
      h3 {
        font-size: px(40);
        font-family: PingFang SC-Bold, PingFang SC;
        font-weight: bold;
        color: #333333;
        margin: 0 0 px(24) 0;
      }
      p {
        font-size: px(32);
        font-family: PingFang SC-Regular, PingFang SC;
        font-weight: 400;
        color: #999999;
      }
    }
    .active-section {
      margin-top: px(40);
      &.first {
        margin-top: px(24);
      }
      .t-char {
        font-size: px(32);
        font-family: PingFang SC-Regular, PingFang SC;
        font-weight: 400;
        color: #999999;
      }
      .v-char {
        font-size: px(32);
        font-family: PingFang SC-Bold, PingFang SC;
        font-weight: bold;
        color: #333333;
      }
      .tag {
        display: inline-block;
        padding: 0 px(16);
        height: px(45);
        background: rgba($color: #0077ff, $alpha: 0.1);
        border-radius: 4px 4px 4px 4px;
        font-size: px(24);
        font-family: PingFang SC-Regular, PingFang SC;
        font-weight: 400;
        color: #0077ff;
        line-height: px(45);
      }
    }
  }
  .container {
    padding: px(40) px(24);
    height: 340px;
    border-radius: 0px 0px 0px 0px;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    .header {
      .header-left {
        font-size: px(120);
        font-weight: bold;
        color: $primary-color;
        opacity: 1;
      }
      .header-right {
        margin-left: px(12);
        .header-date-style {
          margin-top: px(7);
          font-size: px(26);
          font-weight: bold;
        }
      }
    }
    .basic-infor-box {
      padding: px(32) px(24);
      .basic-info-headre {
        padding-bottom: px(24);
        border-bottom: px(1) solid #eeeeee;
        .basic-info-title {
          font-size: px(40);
          font-weight: bold;
        }
        .tag-box-style {
          margin-top: px(24);
          .tag-style {
            margin-right: px(9);
          }
        }
      }
      .basic-info-main {
        .basic-info-main-row {
          padding-top: px(24);
          .basic-info-main-row-label {
            font-size: px(30);
          }
          .basic-info-main-row-value {
            margin-left: px(10);
            font-size: px(30);
            font-weight: bold;
          }
        }
      }
    }
    .card-box {
      margin-top: px(40);
      .card-title-style {
        font-size: px(36);
        font-weight: bold;
      }
      .card-style {
        margin-top: px(12);
        padding: px(24);
        .username-style {
          margin-left: px(24);
          font-size: px(40);
          font-weight: bold;
        }
        .card-photo-style {
          margin-right: px(12);
        }
      }
      .card-style-average {
        width: px(290);
      }
    }
    .comments-list {
      .comments-item {
        .comments-info-box {
          padding-bottom: px(32);
          margin: 0 0 px(32) px(16);
          border-bottom: px(1) solid #eeeeee;

          .comments-username-style {
            font-size: px(32);
            font-weight: bold;
            color: #181818;
          }
          .comments-content-style {
            margin-top: px(8);
            font-size: px(28);
          }
        }
      }
    }
  }
}
.bottom-wrap-box {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100vw;

  background: #ffffff;
  height: px(120);
  padding: 0 px(40);
  box-sizing: border-box;
  .btn-white {
    width: px(323);
    height: px(88);
    background: #ffffff;
    border-radius: px(90);
    border: 1px solid #eeeeee;
    font-size: px(32);
    font-family: PingFang SC-Bold, PingFang SC;
    font-weight: bold;
    color: #333333;
    line-height: px(88);
    text-align: center;
  }
  .btn-blue {
    width: px(323);
    height: px(88);
    background: #0077ff;
    border-radius: px(90);
    font-size: px(32);
    font-family: PingFang SC-Bold, PingFang SC;
    font-weight: bold;
    color: #fff;
    line-height: px(88);
    text-align: center;
  }
}
</style>
